                v-for=\"item in authList\"\r\n                   :key=\"item.id\"\r\n                   :to=\"{path:item.action,query:{id:curId}}\"\r\n                   :v-key=\"item.id\">\r\n        {{item.name}}\r\n      </router-link>\r\n    </ctbHead>\r\n    <div class=\"container\">\r\n      <div class=\"quote\">\r\n        <div class=\"quote-ele\">\r\n          <i></i>系统设置-保险公司设置-线下领取\r\n        </div>\r\n        <div class=\"quote-nav\">\r\n          <router-link :class=\"thCurId==item.id? 'cur':''\"\r\n                       v-for=\"item in threeAuthList\"\r\n                       :key=\"item.id\"\r\n                       :to=\"{path:item.action,query:{id:curId}}\">\r\n            {{item.name}}\r\n          </router-link>\r\n        </div>\r\n      </div>\r\n      <div class=\"search\">\r\n        <el-date-picker type=\"month\"\r\n                        placeholder=\"选择月份\"\r\n                        v-model=\"month\"\r\n                        @change=\"()=>{page=1,init()}\"\r\n                        value-format=\"yyyy-MM-dd\">\r\n        </el-date-picker>\r\n        <el-button type=\"primary\"\r\n                   class=\"addChannel\"\r\n                   @click=\"addChannelVisible=true,status=0\">添加保险公司</el-button>\r\n\r\n      </div>\r\n      <el-table :data=\"list\">\r\n        <el-table-column label=\"保险公司图片\"\r\n                         align='center'>\r\n          <template slot-scope=\"scope\">\r\n            <img :src=\"scope.row.photo\"\r\n                 class=\"photo\" />\r\n          </template>\r\n        </el-table-column>\r\n        <el-table-column label=\"保险公司\"\r\n                         align=\"center\">\r\n          <template slot-scope=\"scope\">\r\n            {{scope.row.company}}\r\n\r\n          </template>\r\n        </el-table-column>\r\n\r\n        <el-table-column label=\"总卡数\"\r\n                         align=\"center\">\r\n          <template slot-scope=\"scope\">\r\n            {{scope.row.total_num}}张\r\n          </template>\r\n        </el-table-column>\r\n        <el-table-column label=\"消耗数量\"\r\n                         align=\"center\">\r\n          <template slot-scope=\"scope\">\r\n            {{scope.row.consume_total}}张\r\n          </template>\r\n        </el-table-column>\r\n        <el-table-column label=\"剩余数量\"\r\n                         align=\"center\">\r\n          <template slot-scope=\"scope\">\r\n            {{scope.row.sur_num}}张\r\n          </template>\r\n        </el-table-column>\r\n        <el-table-column label=\"操作\"\r\n                         align=\"center\">\r\n          <template slot-scope=\"scope\">\r\n            <el-button type=\"danger\"\r\n                       size=\"small\"\r\n                       @click=\"remove(scope.row)\">删除</el-button>\r\n            <el-button type=\"primary\"\r\n                       size=\"small\"\r\n                       @click=\"status=1,Modify(scope.row)\">修改</el-button>\r\n          </template>\r\n        </el-table-column>\r\n      </el-table>\r\n      <el-dialog :title=\"status==0 ? '添加保险公司' : '修改保险公司'\"\r\n                 center\r\n                 :visible.sync=\"addChannelVisible\"\r\n                 width=\"30%\"\r\n                 @close='()=>$refs.channelForm.resetFields()'>\r\n        <el-form label-width=\"120px\"\r\n                 style=\"width:80%\"\r\n                 :model=\"channelList\"\r\n                 :rules=\"channelRules\"\r\n                 ref=\"channelForm\">\r\n          <el-form-item label=\"保险公司名称\"\r\n                        prop=\"name\">\r\n            <el-input placeholder=\"请输入保险公司名称\"\r\n                      v-model=\"channelList.name\"\r\n                      :disabled=\"status==1\" />\r\n          </el-form-item>\r\n          <el-form-item label=\"卡数量\"\r\n                        prop=\"num\">\r\n            <el-input placeholder=\"请输入卡数量\"\r\n                      v-model=\"channelList.num\"\r\n                      :disabled=\"true\" />\r\n          </el-form-item>\r\n          <el-form-item label=\"图片:\"\r\n                        prop=\"photo\">\r\n            <el-upload :action='baseURL+\"admin/Prize/cbImg\"'\r\n                       :data=\"{token:token}\"\r\n                       name='img'\r\n                       list-type=\"picture-card\"\r\n                       :limit='1'\r\n                       :file-list='channelList.photo ? [{url:channelList.photo}] : []'\r\n                       :on-success=\"handleAvatarSuccess\"\r\n                       :on-remove=\"handleRemove\">\r\n              <i class=\"el-icon-plus\"></i>\r\n            </el-upload>\r\n          </el-form-item>\r\n          <el-form-item>\r\n            <el-button type=\"primary\"\r\n                       @click=\"status==0 ? addChannel() : ModifyImg()\"\r\n                       :loading=\"channelLoaading\">{{status==0 ? '添加' : '修改'}}</el-button>\r\n          </el-form-item>\r\n        </el-form>\r\n      </el-dialog>\r\n      <!-- 分页 -->\r\n      <div class=\"page_center\"\r\n           v-show=\"pageCount&&pageCount>1\">\r\n        <paging :page-count=\"pageCount\"\r\n                :page=\"page\"\r\n                @index=\"(e)=>page=e\"></paging>\r\n        <!--分页的组件-->\r\n      </div>\r\n    </div>\r\n  </div>\r\n</template>\r\n<script type=\"text/ecmascript-6\">\r\nimport { getPro, GET_City } from '@/utils'\r\nimport qs from 'qs'\r\nimport obj from '../../components/common'\r\nexport default {\r\n  data () {\r\n    return {\r\n      seCurId: '',\r\n      threeAuthList: [],\r\n      list: [],\r\n      token: window.sessionStorage.getItem('bbytoken'),\r\n      page: 0,\r\n      pageCount: 0,\r\n      authList: [],\r\n      list: [], //保险公司列表\r\n      provinceList: [], //省列表\r\n      currentProvince: '',//当前选中的省\r\n      cityList: [], //市列表\r\n      currentCity: '', //当前选中的市\r\n      addChannelVisible: false, //添加渠道弹框显示状态\r\n      channelList: {  //添加保险公司数据\r\n        name: '', //渠道名称\r\n        num: '500', //卡数量\r\n        photo: '',  //保险公司图片\r\n      },\r\n      channelRules: { //添加保险公司form规则验证\r\n        name: { required: true, message: '请输入渠道名称', trigger: 'blur' },\r\n        num: { required: true, message: '请输入添加的卡数量', trigger: 'blur' },\r\n        photo: { required: true, message: '请上传保险公司图片', trigger: 'blur' },\r\n      },\r\n      channelLoaading: false, //添加保险公司loading\r\n      acitivIndex: 0,\r\n      checkedCompany: [],  //当前勾选的保险公司\r\n      status: 0,   //0 是添加保险公司 1是修改保险公司\r\n      month: '', //月份\r\n    }\r\n  },\r\n  computed: {\r\n    CheckedChannel () {    //选中的渠道\r\n      let CheckedChannel = []\r\n      this.list.forEach(item => {\r\n        if (item.checked) {\r\n          let data = []\r\n          CheckedChannel.push({ channel_id: item.id, num: item.sur_num })\r\n        }\r\n      })\r\n      return CheckedChannel\r\n    },\r\n    dateTime () {\r\n      const dateTime = new Date(this.month)\r\n      const year = dateTime.getFullYear()\r\n      const month = dateTime.getMonth() + 1\r\n      return this.month ? `${year}-${month}` : ''\r\n    }\r\n  },\r\n  methods: {\r\n    async init () {\r\n      try {\r\n        const res = await this.$axios.post('admin/ChannelSetCity/downList', { token: this.token, page: this.page, month: this.dateTime })\r\n        this.list = res.data.data.list || []\r\n        this.pageCount = res.data.data.rows || 0\r\n      } catch (error) {\r\n        throw (error)\r\n      }\r\n\r\n    },\r\n    addChannel () {  //添加保险公司\r\n      this.$refs.channelForm.validate(async valid => {\r\n        if (valid) {\r\n          try {\r\n            let id = this.cityList[this.acitivIndex] && this.cityList[this.acitivIndex].id\r\n            this.channelLoaading = true\r\n            const res = await this.$axios.post('admin/ChannelSetCity/channelAdd', Object.assign(this.channelList, { token: this.token, types: 1 }))\r\n            this.channelLoaading = false\r\n            if (res.data.code == 1) {\r\n              this.$message({ message: res.data.msg, type: \"success\" })\r\n              this.addChannelVisible = false\r\n              this.init()\r\n            } else {\r\n              this.$message.error(res.data.msg)\r\n            }\r\n          } catch (error) {\r\n            this.channelLoaading = false\r\n            throw (error)\r\n          }\r\n        }\r\n      })\r\n    },\r\n    handleAvatarSuccess (res) {  //上传保险公司图片\r\n      this.channelList = Object.assign({}, this.channelList, { photo: res })\r\n    },\r\n    handleRemove () {  //删除保险公司图片\r\n      this.channelList.photo = ''\r\n    },\r\n    Modify (item) {  //修改保险公司图片\r\n      this.addChannelVisible = true\r\n      this.$nextTick(() => {\r\n        this.channelList = Object.assign(this.channelList, item, { name: item.company })\r\n      })\r\n    },\r\n    async ModifyImg () {  //修改\r\n      try {\r\n        this.channelLoaading = true\r\n        const res = await this.$axios.post('admin/ChannelSetCity/updImg', { token: this.token, channel_id: this.channelList.id, photo: this.channelList.photo })\r\n        this.channelLoaading = false\r\n        if (res.data.code == 1) {\r\n          this.$message({ message: res.data.msg, type: \"success\" })\r\n          this.addChannelVisible = false\r\n          this.init()\r\n        } else {\r\n          this.$message.error(res.data.msg)\r\n        }\r\n      } catch (error) {\r\n        this.channelLoaading = false\r\n        throw (error)\r\n      }\r\n    },\r\n    remove (item) {  //删除保险公司\r\n      this.$confirm('此操作将删除该保险公司, 是否继续?', '提示', {\r\n        confirmButtonText: '确定',\r\n        cancelButtonText: '取消',\r\n        type: 'warning'\r\n      }).then(async () => {\r\n        try {\r\n          const res = await this.$axios.post('admin/ChannelSetCity/setStatus', { token: this.token, channel_id: item.id, types: 1 })\r\n          if (res.data.msg) {\r\n            this.$message({ message: res.data.msg, type: \"success\" })\r\n            this.init()\r\n          } else {\r\n            this.$message.error(res.data.msg)\r\n          }\r\n        } catch (error) {\r\n          throw (error)\r\n        }\r\n      }).catch(() => { });\r\n    },\r\n    erAuth () {  //权限匹配\r\n      var id = this.$route.query.id;\r\n      this.curId = id;\r\n      this.$axios.post('admin/Auth/erAuth', {\r\n        token: window.sessionStorage.getItem('bbytoken'),\r\n        id: id\r\n      })\r\n        .then(res => {\r\n          if (res.data.code == 1) {\r\n            var arr = res.data.data;\r\n            for (var i = 0; i < arr.length; i++) {\r\n              if (arr[i].son) {\r\n                if (arr[i].name == '保险公司设置') {\r\n                  this.seCurId = arr[i].id;\r\n                  this.threeAuthList = arr[i].son;\r\n                }\r\n                for (var j = 0; j < arr[i].son.length; j++) {\r\n                  if (arr[i].action != arr[i].son[j].action) {\r\n                    arr[i].action = arr[i].son[0].action;\r\n                  }\r\n                  if (arr[i].son[j].name == '线下领取' && arr[i].name == '保险公司设置') {\r\n                    this.thCurId = arr[i].son[j].id;\r\n                  }\r\n                }\r\n              }\r\n            }\r\n            this.authList = arr;\r\n          } else {\r\n            this.$alert(res.data.msg, '提示', { type: 'error' });\r\n          }\r\n        })\r\n        .catch(err => {\r\n          alert(err);\r\n        })\r\n    }\r\n  },\r\n  mounted () {\r\n    this.erAuth();\r\n    this.init()\r\n\r\n  },\r\n}\r\n</script>\r\n<style scoped>\r\n.photo {\r\n  width: 50px;\r\n  height: 50px;\r\n  vertical-align: middle;\r\n}\r\n.search {\r\n  margin: 20px 50px;\r\n}\r\n.main {\r\n  padding: 0 50px;\r\n}\r\n.main:after {\r\n  display: block;\r\n  content: \"\";\r\n  clear: both;\r\n}\r\n.main > div {\r\n  float: left;\r\n}\r\n.main_item {\r\n  width: 70%;\r\n}\r\n.main_item .el-checkbox {\r\n  line-height: 50px;\r\n}\r\n.main_item .el-col-1 {\r\n  width: 2%;\r\n}\r\n.main_item .el-col-offset-1 {\r\n  margin-